import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			id:'',
			sessionId:'',
			myData:{},
			disabled:true,
			currentTime:''
		}
	},
	onLoad(option) {
		this.id = parseInt(option.id)
		this.sessionId = option.sessionId
		this.init()
		this.currentTime = new Date()
		console.log(this.currentTime,'当前时间')
	},
	methods: {
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/getActivityById',
				data:{
					id:this.id,
					sessionId:this.sessionId
				},
				success: (res) => {
					if(res.data.stat == 1){
						this.myData = res.data.data[0]
						var beginDate = new Date(this.myData.startRegistration);
						var endDate = new Date(this.myData.endRegistration);
						if(this.currentTime >= beginDate && this.currentTime <= endDate){
							this.disabled = false
						}
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		},
		
		joinIn(){
			uni.showModal({
				title: '提示',
				content: '是否确认参加活动',
				success: (res)=> {
					if (res.confirm) {
						console.log(this.sessionId)
						uni.request({
							url:'https://www.nssnail.com/wx/participateActivity',
							method:'POST',
							withCredentials:true,
							data:{
								sessionId:this.sessionId,
								id:this.id
							},
							success: (res) => {
								console.log(res,'res')
								if(res.data.stat == 1){
									uni.showToast({
										title:'报名成功',
										icon:'success',
										duration:3000
									})
								}else{
									uni.showToast({
										title:res.data.message,
										icon:'none',
										duration:3000
									})
								}
								
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		}
	},
	components:{
		uniIcons
	}
}
